export const request = () => ({
  type: DATA_REQUEST,
});

export const receive = ({ json }) => ({
  type: DATA_RECEIVE,
  data: json,
  receivedAt: Date.now(),
});

export const requestNext = () => ({
  type: DATA_REQUEST_NEXT,
});

export const receiveNext = ({ json }) => ({
  type: DATA_RECEIVE_NEXT,
  data: json,
  receivedAt: Date.now(),
});

